      let _this = this;
      this.loading = true;
      _this.$emit("finding", true);
      axios
        .post(`/getGundam/FilterSearch`, this.filterPost, { timeout: 40000 })
        .then(res => {
          // console.log(res);
          _this.$emit("finding", false);
